import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import msui from './msui'

Vue.use(VueRouter)
Vue.use(msui)

const routes = [{
  path: '/',
  redirect: '/home'
}, {
  path: '/home',
  name: 'home',
  component: (resolve) => {
    return require(['./views/home.vue'], resolve)
  },
  meta: {
    title: ''
  }
}, {
  path: '/about',
  name: 'about',
  component: (resolve) => {
    return require(['./views/about.vue'], resolve)
  },
  meta: {
    goback: true,
    title: ''
  }
}, {
  path: '/button',
  name: 'button',
  component: (resolve) => {
    return require(['./views/button.vue'], resolve)
  },
  meta: {
    goback: true,
    title: ''
  }
}, {
  path: '/tabs',
  name: 'tabs',
  component: (resolve) => {
    return require(['./views/tabs.vue'], resolve)
  },
  meta: {
    goback: true,
    title: ''
  }
}, {
  path: '/list',
  name: 'list',
  component: (resolve) => {
    return require(['./views/list.vue'], resolve)
  },
  meta: {
    goback: true,
    title: ''
  }
}, {
  path: '/form',
  name: 'form',
  component: (resolve) => {
    return require(['./views/form.vue'], resolve)
  },
  meta: {
    goback: true,
    title: ''
  }
}, {
  path: '/card',
  name: 'card',
  component: (resolve) => {
    return require(['./views/card.vue'], resolve)
  },
  meta: {
    goback: true,
    title: ''
  }
}, {
  path: '/icon',
  name: 'icon',
  component: (resolve) => {
    return require(['./views/icon.vue'], resolve)
  },
  meta: {
    goback: true,
    title: ''
  }
}, {
  path: '/swiper',
  name: 'swiper',
  component: (resolve) => {
    return require(['./views/swiper.vue'], resolve)
  },
  meta: {
    goback: true,
    title: ''
  }
}, {
  path: '/iscroll',
  name: 'iscroll',
  component: (resolve) => {
    return require(['./views/iscroll.vue'], resolve)
  },
  meta: {
    goback: true,
    title: 'iscroll'
  }
}, {
  path: '/modal',
  name: 'modal',
  component: (resolve) => {
    return require(['./views/modal.vue'], resolve)
  },
  meta: {
    goback: true,
    title: ''
  }
}, {
  path: '/iscroll2',
  name: 'iscroll2',
  component: (resolve) => {
    return require(['./views/iscroll2.vue'], resolve)
  },
  meta: {
    goback: true,
    title: 'iscroll2'
  }
}, {
  path: '/preloader',
  name: 'preloader',
  component: (resolve) => {
    return require(['./views/preloader.vue'], resolve)
  },
  meta: {
    goback: true,
    title: ''
  }
}]

const router = new VueRouter({
  routes //  routes: routes
})

// 
var IS_ROUTER_FIRST_CHANGE = true

router.beforeEach((to, from, next) => {

  let direction = 'slide-fade'
    // 
  if (!!from.meta.goback) {
    // callback
    if (!!to.meta.goback) {
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      direction = toDepth >= fromDepth ? 'slide-right' : 'slide-left'
    } else {
      direction = 'slide-left'
    }
  } else {
    // 
    direction = !!to.meta.goback ? 'slide-right' : 'slide-fade'
  }

  if (IS_ROUTER_FIRST_CHANGE) {
    IS_ROUTER_FIRST_CHANGE = !IS_ROUTER_FIRST_CHANGE
    direction = 'slide-fade'
  }

  router.app.pageTransition = direction

  document.title = to.meta.title

  next()

})


const app = new Vue({
  router,
  template: '<App/>',
  components: {
    App
  }
}).$mount('#app')


